<template>
  <div class="about-container" v-loading="loading || !iframeLoading">
    <!-- loading是否显示， 取决于当前当loading是否为true，或者iframeLoading加载完成 -->
    <iframe
      :src="src.src"
      frameborder="0"
      @load="iframeLoading = true"
    ></iframe>
    <!-- 当iframe加载完成之后，触发load事件，将iframesLoading设为ture，默认是false -->
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      iframeLoading: false,
    };
  },
  computed: {
    ...mapState("about", {
      src: "data",
      loading: "loading",
    }),
  },
  created() {
    this.$store.dispatch("about/fetchAbout");
  },
};
</script>

<style scoped lang="less">
.about-container {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
  iframe {
    width: 100%;
    height: 100%;
  }
}
</style>
